import React, { useEffect, useState, useRef } from "react"
import Box from "@material-ui/core/Box"
import { getCookie, stickerChooser, stickerDict } from './utils';
import { Select, MenuItem, FormControl, Button, TextField, InputLabel, Grid } from "@material-ui/core";
import { sendMessage, sendMessageSticker, Messages, setPfpUrl } from "./messaging";
import { SERVER_HOST } from "./global-config";
import { useNavigate, useSearchParams } from 'react-router-dom';
import { decodeJwt } from "./utils";
import { Rooms } from "./room-button";
import EmojiPicker from 'emoji-picker-react';
import { getUserRooms } from "./clients/user-client";

export const ChatBox = () => {
    const messageField = useRef();

    const navigate = useNavigate();

    const [messages, setMessages] = useState([])
    const [rooms, setRooms] = useState([])

    const [queryParameters] = useSearchParams()
    const [user, setUser] = useState("")

    const updateMessages = (data) => {
        const parsedData = JSON.parse(data);
        setMessages(oldArray => [...oldArray, parsedData]);
    }

    const sendMsg = () => {
        if (messageField.current.value.trim().length > 0) {
            sendMessage(messageField, queryParameters.get("chatId"));
            messageField.current.value = ""
        }
    }

    const createRoom = (roomName, roomId, image = setPfpUrl(), hasUnreadMessages = false) => {
        if (rooms.filter(r => r.description == roomName).length === 0) {
            const roomData = {
                image: image,
                href: "/chat?chatId=" + roomId,
                description: roomName,
                hasUnreadMessages: hasUnreadMessages
            };
            setRooms(prevRooms => [...prevRooms, roomData]);
        }
    }

    useEffect(async () => {
        const token = getCookie("Authorization");
        const setStateUser = async () => {
            const user = await decodeJwt(token);
            setUser(user);
            createRoom("Saved Messages", user.id)
        }
        await setStateUser()



        const mapRoomsArrray = async () => {
            await getUserRooms().then(roomResponse => {
                const user = decodeJwt(token);
                roomResponse.map(room => {
                    let chatId = room.chatId;
                    let chatName = room.name;
                    if (room.isDirectMessages || true) {
                        console.log(user)
                        chatId = chatId.replace(user.id, "")
                        chatId = chatId.replace(":", "")

                        chatName = chatName.replace(user.userName, "")
                        chatName = chatName.replace(":", "")
                    }
                    createRoom(chatName, chatId, setPfpUrl(room.image), room.hasUnreadMessages)
                });
            });
        }
        await mapRoomsArrray()


        if (token === null) {
            navigate("/")
        }
        const eventSource = new EventSource(`${SERVER_HOST}/chatAlive?authToken=${token}&chatId=${queryParameters.get("chatId")}`, {
            headers: {
                'Authorization': token
            }
        }, [user, rooms]);

        eventSource.onmessage = (e) => updateMessages(e.data);

    }, []);

    const handleEnterDown = event => {
        if (event.key === "Enter" && !event.shiftKey) {
            event.preventDefault();
            sendMsg();
        }
    };

    const handleChange = event => {
        console.log(event.target.value)
        console.log(event.target.value)
        sendMessageSticker(stickerChooser(event.target.value), queryParameters.get("chatId"));
        event.target.value = 0;
    }

    const handleEmojiClick = event => {
        messageField.current.value += event.emoji;
    }

    return (
        <Box className="column-container main-class">
            <Box className="row-container chat-ui">
                <Messages messages={messages} roomEvent={createRoom} />
                <Rooms rooms={rooms} />
            </Box>
            <Box className='row-container send-controls'>
                <TextField inputRef={messageField} multiline label="Message" onKeyDown={handleEnterDown} id='messageField' className='textfield messageField'></TextField>
                <FormControl className="stickers-select" variant="outlined">
                    <InputLabel></InputLabel>
                    <Select label=""><EmojiPicker emojiStyle='native' onEmojiClick={handleEmojiClick} /></Select>
                </FormControl>
                <FormControl className="stickers-select" variant="outlined">
                    <Select
                        labelId="sticker-select-label"
                        id="stickers-select"
                        label="Stickers"
                        value="0"
                        onChange={handleChange}
                    >
                        <MenuItem value={0} disabled>Stickers</MenuItem>

                        {Object.keys(stickerDict).map(key => {
                            console.log(key)
                            return <MenuItem key={key} value={key}>
                                <img className="message-user-image" src={stickerDict[key]} alt={`Sticker ${key}`} />
                            </MenuItem>
                        })}
                    </Select>
                </FormControl>
                <Button onClick={sendMsg} className='send-button' align="center" color="primary" variant="text">
                    SEND MESSAGE
                </Button>
            </Box>
        </Box>
    )
}