import { getCookie, decodeJwt, checkIfImgUrl, refreshPage } from './utils';
import { AUTHORIZATION_HEADER, DEFAULT_PFP_URL, SERVER_HOST } from './global-config';
import Typography from '@mui/material/Typography';
import React, { useEffect, useRef } from "react"
import Box from "@material-ui/core/Box"
import { Card } from '@mui/material';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import Markdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import UserProfileModal from './user-profile-card';

export function setPfpUrl(url) {
    return checkIfImgUrl(url) != null ? url : DEFAULT_PFP_URL;
}

const MessageCard = ({message}) => {
    const [open, setOpen] = React.useState(false);
    const [modalPosition, setModalPosition] = React.useState({ x: 0, y: 0 });
    const windowHeight = React.useRef(window.innerHeight);
    const windowWidth = React.useRef(window.innerWidth);

    const openUserModal = (event) => {
        const mousePositionX = event.clientX;
        const mousePositionY = event.clientY;
        console.log(windowWidth)
        console.log(windowHeight)

        let modalOverlapX = mousePositionX + 260;
        let modalOverlapY = mousePositionY + 420;

        modalOverlapX = modalOverlapX > windowWidth.current ? mousePositionX - 250 : mousePositionX; 
        modalOverlapY = modalOverlapY > windowHeight.current ? mousePositionY - 400 : mousePositionY;

        setModalPosition({ x: modalOverlapX, y: modalOverlapY });
        setOpen(true);
    }

    return <Card key={message.id} className='message-card'>
        <UserProfileModal message={message} open={open} setOpen={setOpen} mousePosition={modalPosition}></UserProfileModal>
        <div className='column-container message-card-profile'>
            <Button className='user-image-button' onClick={openUserModal}><img width={50} className='userImage' src={setPfpUrl(message.pfpUrl)}></img></Button>
        </div>
        <div className='column-container'>
            <div className='row-container'>
                <Typography className='message-header-label'>
                    {message.name || 'unnamed'}
                </Typography>
                <Typography className='message-header-label' sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    {message.time}
                </Typography>
            </div>
            <Typography className='content-label' variant="body2">
                {checkIfImgUrl(message.content) ? (<img className='content-image' src={message.content} />) :
                    <Markdown remarkPlugins={[remarkGfm]} >{message.content}</Markdown>}
            </Typography>
        </div>
    </Card>
};

export const Messages = ({ messages, roomEvent }) => {
    const messagesEndRef = useRef(null);

    const scrollToBottom = () => {
        messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    };
    useEffect(scrollToBottom, [messages]);

    return (
        <Box id='chat' className='scrollable-chat scroll-style'>
            {messages.map(message => {
                return processMessage(message, roomEvent);
            })}
            <div id='chatEnd' style={{ float: "left", clear: "both" }}
                ref={messagesEndRef}>
            </div>
        </Box>
    );
};

const processMessage = (d, roomEvent) => {
    switch (d.type) {
        case "CHAT_MESSAGE":
            return <MessageCard message={d.message}/>
        case "NOTIFICATION_MESSAGE":
            return <Notification message='You have new Direct Messages! Check it out!' />
        case "NEW_ROOM":
            return <Notification message='New room was created' callBack={() => {
                const user = decodeJwt(getCookie(AUTHORIZATION_HEADER));
                let chatId = d.message.room.chatId;
                let chatName = d.message.room.name;
                if (d.message.room.isDirectMessages || true) {
                    console.log(d)
                    chatId = chatId.replace(user.id, "")
                    chatId = chatId.replace(":", "")

                    chatName = chatName.replace(user.userName, "")
                    chatName = chatName.replace(":", "")
                }
                return roomEvent(chatName, d.userId, setPfpUrl(d.pfpUrl), true)
            }} />
    }
}

const Notification = ({ message, callBack }) => {
    const { enqueueSnackbar } = useSnackbar();

    useEffect(() => {
        enqueueSnackbar(message)
        if (callBack != null) {
            callBack();
        }
    }, [])
    return;
}

const url = `${SERVER_HOST}/post`;

export function sendMessageSticker(img, chatId) {
    const token = getCookie(AUTHORIZATION_HEADER);
    const user = decodeJwt(token);
    sendMsg(user, img, chatId, token)
}

export function sendMessage(messageField, chatId) {

    const token = getCookie(AUTHORIZATION_HEADER);
    const user = decodeJwt(token);
    sendMsg(user, messageField.current.value, chatId, token)
}

const sendMsg = (user, message, chatId, token) => {
    fetch(url, {
        method: 'POST',
        mode: 'cors',
        headers: {
            'Authorization': token,
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            content: `${message}`,
            name: `${user.userName}`,
            pfpUrl: `${user.pfpUrl}`,
            chatId: chatId
        })
    })
}
