import { refreshPage } from './utils';
import { Button, Card, CardContent, Box } from '@material-ui/core';
import { CardMedia } from '@mui/material';
import { Link } from 'react-router-dom';
import { useEffect } from 'react';
import { DEFAULT_PUBLIC_IMAGE } from './global-config';

export const RoomButton = ({ room, key }) => {

    useEffect(() => {
        console.log(room.image)
    }, [])

    return <Button key={crypto.randomUUID()} className="room-button" component={Link} onClick={refreshPage} underline='hover' to={room.href} color="inherit" variant="text">
        <Card className="row-container room-card">
            <CardMedia component="img" sx={{ width: 50 }}
                image={room.image} />
            <CardContent>{room.description}</CardContent>
            {
                room.unreadMessages &&
                <CardContent>HAS NEW MESSAGES</CardContent>
            }
        </Card>
    </Button>;
}

export const Rooms = ({ rooms }) => {

    useEffect(() => {
        rooms.push({
            image: DEFAULT_PUBLIC_IMAGE,
            href: "/chat",
            description: "General"
        })
    }, [])

    return <Box className="room-select column-container scroll-style">
        {rooms.map(room => {
            return <RoomButton key={room.id} room={room} />
        })}
    </Box>
}