import { SERVER_HOST } from "../global-config";
import { decodeJwt, getCookie } from "../utils";

export function getUserRooms() {
    var url = `${SERVER_HOST}/user/rooms`;
    const token = getCookie("Authorization");
    const user = decodeJwt(token);
    return fetch(url, {
        method: 'GET',
        mode: 'cors',
        headers: {
            'Authorization': token,
            "Content-Type": "application/json"
        }
    }).then(rooms => {
        return rooms.json();
    })
}