import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { setPfpUrl } from './messaging';
import { refreshPage } from './utils';
import { Link } from 'react-router-dom';

export default function UserProfileModal({ message, open, setOpen, mousePosition }) {
    const handleClose = () => setOpen(false);

    return (
        <Modal
            className='user-profile-modal'
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            style={{
                position: 'absolute',
                top: mousePosition.y + 10, //      
                left: mousePosition.x + 10, //      
                right: 'auto',
                bottom: 'auto'
            }}
        >
            <Box >
                {/*   */}
                <Typography className='user-modal-header'>
                    <Typography variant="overline" color={"#fff"}>  </Typography>
                </Typography>
                {/*  */}
                <Typography id="modal-modal-title" variant="h6" component="h2">
                    <img style={{
                        "margin": "auto",
                        "display": "block"
                    }} height={180} src={setPfpUrl(message.pfpUrl)}></img>
                </Typography>
                {/*   */}
                <Typography color={"#fff"} className="modal-user-name">
                    {message.name || 'unnamed'}
                </Typography>
                {/*   */}
                <Typography className='user-modal-header'>
                    <Typography variant="overline" color={"#fff"}> :</Typography>
                </Typography>
                <Typography color={"#fff"} className="modal-user-name">
                     -  
                </Typography>
                {/*     */}
                <Typography className='modal-pm-button' variant="body2">
                    <Button onClick={refreshPage} component={Link} to={"?chatId=" + message.userId} gutterBottom>
                        <Typography sx={{ fontSize: 12 }}> private message  </Typography>
                    </Button>
                </Typography>
            </Box>
        </Modal>
    );
}