import Typography from '@mui/material/Typography';
import React from "react"
import Box from "@material-ui/core/Box"
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField"
import logo from '../logo.png';
import { useNavigate } from 'react-router-dom';
import { FormControl } from '@material-ui/core';
import { setCookie } from './utils';
import { Link, Tab, Tabs } from '@mui/material';
import { AUTHORIZATION_HEADER, SERVER_HOST } from './global-config';


function CustomTabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

export const RegisterForm = () => {
    const navigate = useNavigate();
    const nameField = () => document.getElementById('nameField');
    const imgUrlField = () => document.getElementById('imgUrlField');
    const passwordField = () => document.getElementById('passwordField');

    const register = () => {

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ userName: nameField().value, pfpUrl: imgUrlField().value, password: passwordField().value })
        };

        fetch(`${SERVER_HOST}/token/register`, requestOptions)
            .then(response => {
                if (response.status === 200) {
                    console.log("Success Registration");
                    setCookie(AUTHORIZATION_HEADER, response.headers.get(AUTHORIZATION_HEADER));
                    navigate('/chat');
                } else {
                    console.log("Failure Registration")
                }
            });
    };

    const auth = () => {

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ userName: nameField().value, password: passwordField().value })
        };

        fetch(`${SERVER_HOST}/token/auth`, requestOptions)
            .then(response => {
                if (response.status === 200) {
                    console.log("Success Authorization");
                    setCookie(AUTHORIZATION_HEADER, response.headers.get(AUTHORIZATION_HEADER));
                    navigate('/chat');
                } else {
                    console.log("Failure Authorization")
                }
            });
    };

    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box>
            <Tabs style={{"margin": "auto"}} className='row-container' value={value} onChange={handleChange} aria-label="basic tabs example">
                <Tab label="Register" />
                <Tab label="Login" />
            </Tabs>
            <CustomTabPanel value={value} index={0}>
                <FormControl role="tabpanel" className='login-container'>
                    <Typography>Welcome! Type your user data below!</Typography>
                    <div className="App-logo">
                        <img src={logo} alt="logo" />
                    </div>
                    <TextField required id='imgUrlField' label='Image Url' className='login-field'></TextField>
                    <TextField required id='nameField' label='User Name' className='login-field'></TextField>
                    <TextField required id='passwordField' type='password' label='Password' className='login-field'></TextField>
                    <Button type='submit' onClick={register} className='login-field'>Register</Button>
                </FormControl>
            </CustomTabPanel>
            <CustomTabPanel value={value} index={1}>
                <FormControl role="tabpanel" className='login-container'>
                    <Typography>Type your login and password</Typography>
                    <div className="App-logo">
                        <img src={logo} alt="logo" />
                    </div>
                    <TextField required id='nameField' label='User Name' className='login-field'></TextField>
                    <TextField required id='passwordField' type='password' label='Password' className='login-field'></TextField>
                    <Button type='submit' onClick={auth} className='login-field'>Login</Button>
                </FormControl>
            </CustomTabPanel>
        </Box >
    )
} 